<style type="text/css">
.add-btn {
  transform: translateY(-50%);
  z-index: 9999;
  right: 0.16rem;
}

.populars {
  position: relative;
  overflow: visible;
}

.carrousel {
  margin-top: 0.16rem;
}

</style>
<template>
  <div class="scroll-h bg-200 ht-max">
    <section class="wd-page justify-c">
      <div class='sd-card ht-dialog carrousel' v-if='seen'></div>
      <div class="sd-card populars">
        <div class="ht-m pd-m-h bg-50">热门文章</div>
        <ul class="lay-h">
          <span class="ht-xl wd-xl lay-f sd-cut gap bg-50" v-for='item in populars' @click.prevent='find(item._id)'>
              <div class='ht-xs float-nw bg-gradient wd-max align-indent'>{{item.title}}</div>
              <div class="cliper wd-max">
                <img :src="item.cover" @error='item.cover = "static/img/img01.png"'>
              </div>
            </span>
        </ul>
        <span class="align-v wave ht-l theme-mark wd-l align-c radius-l float-ne sd-card add-btn" @click='$router.push("/blogs/upadd")'>
          <icon name='plus'></icon>
        </span>
      </div>
      <div class="sd-list sd-card gap-v">
        <div class="ht-m pd-m-h bg-50">
          <span>文章列表</span>
        </div>
        <li class="ht-l lay-h wave" v-for='item in blogs' @click.prevent='find(item._id)'>
          <span class='cliper wd-l'>
              <img :src="item.cover" @error='item.cover = "static/img/img01.png"'>
            </span>
          <span class="wd-auto pd-m-h">{{item.title}}</span>
          <span class="wd-xl align-r pd-m-h">{{item.content}}</span>
        </li>
      </div>
    </section>
    <div></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      blogs: [],
      populars: [],
      seen: false,
    }
  },
  mounted() {
    this.list()
    this.seen = true
    setTimeout(t => {
      this.initCarrousel()
    }, 50)
  },
  beforeDestroy() {
    this.carrousel.paused = true
  },
  methods: {
    list() {
      this.$http.post('/api/blogs/list')
        .then(res => {
          this.blogs = res.body
          this.populars = res.body.slice(0, 4)
        })
    },
    find(_id) {
      this.$router.push({ path: '/blogs/upadd', query: { _id: _id } })
    },
    remove(_id) {
      this.$http.post('/api/blogs/remove', {
        _id: _id
      }).then(res => {
        this.pd.toast('删除成功')
        this.list()
      })
    },
    initCarrousel() {
      this.carrousel = new Carrousel({
        imgs: [{
          url: 'static/img/img02.png',
          title: '未来实际能力培养',
          href: '#',
        }, {
          url: 'static/img/img03.png',
          title: '未来实际能力培养',
          href: '#',
        }, {
          url: 'static/img/img04.png',
          title: '未来实际能力培养',
          href: '#',
        }],
        outer: document.querySelector('.carrousel'),
      })
    }
  }
}

</script>



