<style type="text/css" scoped>
.gap {
  margin: 0.08rem;
}

.sd-card {
  flex: 0 1 calc(20% - 0.2rem);
}

.fa-icon{
  transform-origin: 0 100%;
  transform: scale(3);
  fill: #FFEB3B;
}

</style>
<template>
  <div class="scroll-h bg-200 ht-max">
    <section class="wd-page justify-c">
      <div class="gap-v">
        <ul class="lay-h">
          <span class="lay-f sd-cut bg-50 sd-card gap wave" v-for='item in projects' @click.prevent='href(item)'>
              <div class='ht-xs float-nw bg-gradient wd-max align-indent'>{{ item }}</div>
              <div class="wd-max cliper">
                <icon name = 'folder' class='gap'></icon>
              </div>
            </span>
        </ul>
      </div>
    </section>
  </div>
</template>
<script>
export default {
  data() {
    return {
      projects: []
    }
  },
  mounted() {
    this.list()
  },
  methods: {
    list() {
      this.$http.post('/api/projects/list')
        .then(res => {
          this.projects = res.body
        })
    },
    href(item) {
      this.$router.push({
        path: '/projects/detail',
        query: {
          href:item
        }
      })
    }
  }
}

</script>



