<template>
  <article class="bg-200 ht-max">
    <header class="ht-l sd-card">
      <section class="wd-page justify-c">
        <li class="lay-h">
          <button class='wd-m' @click='$router.go(-1)'>
            <icon name='chevron-left'></icon>
          </button>
          <h1 class='wd-auto fs-l'>我的博客</h1>
          <button class='wd-m' @click='remove'>
            <icon name='trash'></icon>
          </button>
          <button class='wd-m' @click='save'>
            <icon name='save'></icon>
          </button>
        </li>
      </section>
    </header>
    <content id='scroller'>
      <section class="wd-page justify-c">
        <ol class="sd-card gap-v">
          <li class="ht-l lay-h">
            <label class="pd-m-h wd-l">标题</label>
            <input class="ht-m sd-cut wd-dialog pd-m-h" type="text" name="title" v-model='blog.title'>
          </li>
        </ol>
        <ul class='pd-s sd-card gap-v'>
          <textarea class="bg-50 pd-s sd-cut" id='content' v-model='blog.content'></textarea>
        </ul>
        <ol class="sd-list sd-card">
          <li class="ht-l lay-h">
            <label class="pd-m-h wd-l">选择封面</label>
            <input class="ht-m sd-cut wd-dialog pd-m-h" type="text" disabled="disabled">
            <span class="wd-l wave sd-cut ht-m img-slct-btn align-c align-v" @click='selectFile'>
              <icon name='image'></icon>
            </span>
            <input type="file" id="file-selector" @change='upload' accept="image/gif, image/jpeg">
          </li>
          <div id='img-preview'><img :src="blog.cover"></div>
        </ol>
      </section>
    </content>
  </article>
</template>
<style scoped>
input[type=file] {
  display: none;
}
.img-slct-btn,
input{
  margin:0.08rem 0;
}

textarea {
  margin: 0;
  height: 3rem;
  display: block;
  width: calc(100% - 0.16rem);
}

#img-preview {
  height: 1rem;
  line-height: 1rem;
  overflow: hidden;
  display: block;
}

#img-preview img {
  height: 100%;
  width: auto;
}

</style>
<script>
export default {
  data() {
    return {
      blog: {
        title: '新的博客',
        content: '内容',
        cover: '',
      }
    }
  },
  mounted() {
    let _id = this.$route.query._id
    if (typeof(_id) === 'undefined') {
      this.blog = {
        title: '新的博客',
        content: '内容',
        cover: '',
        _id: '',
      }
    } else {
      this.$http.post('/api/blogs/find', { _id: _id })
        .then(res => {
          this.blog = res.body
        })
    }
  },
  methods: {
    save() {
      this.$http.post('/api/blogs/upadd', {
        blog: {
          _id: this.blog._id,
          title: this.blog.title,
          content: this.blog.content,
          cover: this.blog.cover
        }
      }).then(res => {
        util.toast('操作成功')
        this.$router.go(-1)
      })
    },
    selectFile() {
      document.getElementById('file-selector').click()
    },
    remove() {
      this.$http.post('/api/blogs/remove', {
        _id: this.blog._id
      }).then(res => {
        util.toast('删除成功')
        this.$router.go(-1)
      })
    },
    upload(event) {
      let file = event.target.files[0]
      if (!file) return
      var formData = new FormData()
      formData.append('file', file)
      util.toast('上传中...')
      this.$http.post('/api/files/upload', formData)
        .then(res => {
          var result = res.body
          if (!result) return
          this.blog.cover = res.body
          util.toast('上传成功')
          event.target.value = null
        })
    }
  },
}

</script>



