<style type="text/css" scoped>
  header{
    height: 0.48rem;
    line-height: 0.48rem;
  }
  iframe{
    height: calc(100% - 0.48rem)
  }
</style>
<template>
  <div class="ht-max">
    <header class="ht-m theme-dark wd-max lay-h">
      <span class="wd-m align-v align-c wave" @click='$router.go(-1)'>
        <icon name='chevron-left'></icon>
      </span>
      <span>{{ $route.query.href }}</span>
    </header>
    <iframe :src="'static/webStudy/' + $route.query.href + '/index.html'" class="ht-max wd-max"></iframe>
  </div>
</template>
<script>
export default {
  data() {
    return {
    }
  },

}
</script>



