<style type="text/css" scoped>
  .content{
    height: calc(100% - 0.48rem);
  }
  header{
    height: 0.48rem;
    line-height: 0.48rem;
  }
  span{
    width: 0.48rem;
    margin-right: 0.08rem;
    cursor: pointer;
    text-align: center;
  }
</style>
<template>
  <div class="ht-max">
    <header class="theme-dark">
      <li class="lay-h wd-page justify-c">
        <span class="wd-l align-v">
          <icon name='leaf'></icon>
        </span>
        <span class="wd-m align-c wave" @click ='blog'>博客</span>
        <span class="wd-m align-c wave" @click='projects'>项目</span>
        <span class="wd-m align-c wave" @click='about'>关于</span>
      </li>
    </header>
    <div class="content">
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      cur:'blog'
    }
  },
  methods: {
    about() {
      this.$router.push('/about')
    },
    blog(){
      this.$router.push('/blogs/list')
    },
    projects(){
      this.$router.push('/projects/list')
    }
  }
}
</script>



